
body,html {
	margin:0;
	height:100%;
	width:100%;
}

#map {
	height: 100%;
	// height: 100vh;
}

body {
    display: grid;
	grid-template-rows: 50px 1fr;
	grid-template-columns: 200px 1fr;
	grid-template-areas: 
	"header header"
	"sidebar content";
}

.header {
	padding:10px;
	grid-area: header;
}
.sidebar {
	grid-area: sidebar;
}
.content {
	grid-area: content;
}
